import { getMaintenanceMode } from "@/util/Maintenance";
import { getInstance } from "./Auth";

export const routeGuard = async (to: { fullPath: any }, _from: any, next: any) => {
  // const maintenanceMode = await getMaintenanceMode();
  // if (maintenanceMode) {
  //   next({ name: "maintenancePage" });
  // }
  const authService = getInstance();

  const guardAction = () => {
    if (authService.isAuthenticated.value) {
      return next();
    } else {
      authService.loginWithRedirect({ appState: { targetUrl: to.fullPath } });
    }
  };

  // If the Auth0Plugin has loaded already, check the authentication state
  if (!authService.loading.value) {
    return guardAction();
  }
};
